const faq = {
  state: {
    faqInfo: [{
      id: 0,
      question: '',
      answer: 'idea'
    }, {
      id: 1,
      question: '',
      answer: 'issue'
    }, {
      id: 2,
      question: '',
      answer: ''
    }, {
      id: 3,
      question: '',
      answer: ''
    }]
  },
  mutations: {
    SET_FAQ_INFO (state, faqInfo) {
      state.faqInfo = faqInfo
    }
  },
  actions: {
    SetFaqInfo ({commit}, faqInfo) {
      commit('SET_FAQ_INFO', faqInfo)
    }
  }
}
export default faq



